
    const imports = ["import React from 'react';","import { Chart, Axis, Tooltip, Geom, Coord, Legend } from 'bizcharts';","import { Row, Col, Statistic, Table } from 'antd';"];
    
    export function getChartComponent(chartType) {
      
      if (chartType === 'line') {
        return `return (
  <Chart
    scale={{
      x: {
        tickCount: 8,
      },
    }}
    height={400}
    data={stackedChartData(resultSet)}
    forceFit
  >
    <Axis name="x" />
    <Axis name="measure" />
    <Tooltip
      crosshairs={{
        type: 'y',
      }}
    />
    <Geom type="line" position="x*measure" size={2} color="color" />
  </Chart>
);
`;
      }
    

      if (chartType === 'x') {
        return ``;
      }
    

      if (chartType === 'tickCount') {
        return ``;
      }
    

      if (chartType === 'type') {
        return ``;
      }
    

      if (chartType === 'bar') {
        return `return (
  <Chart
    scale={{
      x: {
        tickCount: 8,
      },
    }}
    height={400}
    data={stackedChartData(resultSet)}
    forceFit
  >
    <Axis name="x" />
    <Axis name="measure" />
    <Tooltip />
    <Geom type="interval" position="x*measure" color="color" />
  </Chart>
);
`;
      }
    

      if (chartType === 'x') {
        return ``;
      }
    

      if (chartType === 'tickCount') {
        return ``;
      }
    

      if (chartType === 'area') {
        return `return (
  <Chart
    scale={{
      x: {
        tickCount: 8,
      },
    }}
    height={400}
    data={stackedChartData(resultSet)}
    forceFit
  >
    <Axis name="x" />
    <Axis name="measure" />
    <Tooltip
      crosshairs={{
        type: 'y',
      }}
    />
    <Geom type="area" position="x*measure" size={2} color="color" />
  </Chart>
);
`;
      }
    

      if (chartType === 'x') {
        return ``;
      }
    

      if (chartType === 'tickCount') {
        return ``;
      }
    

      if (chartType === 'type') {
        return ``;
      }
    

      if (chartType === 'pie') {
        return `return (
  <Chart height={400} data={resultSet.chartPivot()} forceFit>
    <Coord type="theta" radius={0.75} />
    {resultSet.seriesNames().map((s) => (
      <Axis name={s.key} />
    ))}
    <Legend position="right" />
    <Tooltip />
    {resultSet.seriesNames().map((s) => (
      <Geom type="interval" position={s.key} color="category" />
    ))}
  </Chart>
);
`;
      }
    

      if (chartType === 'height') {
        return ``;
      }
    
    }
    
    export function getCommon() {
      return `const stackedChartData = (resultSet) => {
  const data = resultSet
    .pivot()
    .map(({ xValues, yValuesArray }) =>
      yValuesArray.map(([yValues, m]) => ({
        x: resultSet.axisValuesString(xValues, ', '),
        color: resultSet.axisValuesString(yValues, ', '),
        measure: m && Number.parseFloat(m),
      }))
    )
    .reduce((a, b) => a.concat(b), []);
  return data;
};
`;
    }
    
    export function getImports() {
      return imports;
    }
  