import { useState, useEffect } from 'react';
import { getCodesandboxFiles, getDependencies } from './codegen';
import ChartContainer from './ChartContainer';
import BizchartsCharts from './bizcharts-charts/src/components/ChartRenderer';
import RechartsCharts from './recharts-charts/src/components/ChartRenderer';
import D3Charts from './d3-charts/src/components/ChartRenderer';
import ChartjsCharts from './chartjs-charts/src/components/ChartRenderer';

/*
const libs = {};
*/
const libs = {
  bizcharts: BizchartsCharts,
  recharts: RechartsCharts,
  d3: D3Charts,
  chartjs: ChartjsCharts
};

const App = () => {
  const [query, setQuery] = useState(null);
  const [pivotConfig, setPivotConfig] = useState(null);
  const [library, setLibrary] = useState(null);
  const [chartType, setChartType] = useState(null);
  useEffect(() => {
    window['__cubejs'] = {
      getCodesandboxFiles,
      getDependencies
    };
    const event = new CustomEvent('cubejsChartReady');
    event.initEvent('cubejsChartReady', true);
    document.body.dispatchEvent(event);
    document.body.addEventListener('cubejs', event => {
      const {
        query,
        chartingLibrary,
        chartType,
        pivotConfig
      } = event.detail;

      if (query) {
        setQuery(query);
      }

      if (pivotConfig) {
        setPivotConfig(pivotConfig);
      }

      if (chartingLibrary) {
        setLibrary(chartingLibrary);
      }

      if (chartType) {
        setChartType(chartType);
      }
    });
  }, []);
  return <div className="App">
      {libs[library]?.[chartType] ? <ChartContainer renderFunction={libs[library][chartType]} query={query} pivotConfig={pivotConfig} /> : null}
    </div>;
};

const API_URL = 'http://localhost:4000/cubejs-api/v1';
const CUBEJS_TOKEN = 'secret';
export default App;